#search-box {
  width: 90%;
  outline: none;
  border: none;
  background-color: #3d78b4;
  height: 95%;
  border-radius: 10px;
  padding-left: 15px;
  color: whitesmoke;
}
::placeholder {
  color: whitesmoke;
  opacity: 0.7; /* Firefox */
}
.browser-btns {
  width: 25px;
  height: 25px;
  background-color: #d7ebfd;
  border-radius: 50%;
  margin: 0px 5px;
  font-size: 16px;
  background-position: center;
  background-size: cover;
  padding: 2px;
}

#back {
  background-image: url("../images/back.png");
}
#reload {
  background-image: url("../images/reload.png");
}

#search-btn {
  background-image: url("../images/search.png");
}
